* {
  font-family: 'Roboto';
}

.skill-icon {
  margin: 7px;
  cursor: pointer;
  opacity: 0.5;
  filter: alpha(opacity=50);
  position: relative;
  top: 0px;
  transition: transform .2s ease-in-out;
}
.skill-icon:hover {
  opacity: 1.0;
  filter: alpha(opacity=100); /* For IE8 and earlier */
  transform: translate3d(0px, -10px, 0px);
}